import React, {
  Component, 
} from "react";
import './../App';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { Col, Container, ListGroup, Placeholder, Row, Table } from 'react-bootstrap'

class LostArkClass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: [],
      loading: false,
      skillsName: [],
      skillsLevel: [],
      gameplayEn: [],
      stats: [],
      Engravings: [],
      T1: [],
      T2: [],
      T3: [],
      runes: [],
      runesSkill: [],
      Gems: [],
      Card: [],
    };
  }
  CallApi() {
    fetch("http://localhost:9000/API/Demoniste",)
    .then(res => res.json())
    .then(
      (result) => {
          this.setState({
            isLoaded: true,
            skillsName: result.skills.name,
            skillsLevel: result.skills.level,
            gameplayEn: result.gameplayEn,
            stats: result.stats,
            Engravings: result.Engravings,
            T1: result['Gear Sets'].Tier1,
            T2: result['Gear Sets'].Tier2,
            T3: result['Gear Sets'].Tier3,
            runes: result.Runes.Runes,
            runesSkill: result.Runes.Skills,
            Gems: result.Gems,
            Card: result.Card,
          });
      },
      // Remarque : il est important de traiter les erreurs ici
      // au lieu d'utiliser un bloc catch(), pour ne pas passer  la trappe
      // des exceptions provenant de rels bugs du composant.
      (error) => {
        this.setState({
          isLoaded: true,
          error: "Une erreur est survenue"
        });
      }
    )
  }
  componentDidMount() {
    this.CallApi();
  }
  render() {
    const { error, isLoaded, skillsName, skillsLevel, Card, Engravings, T1 ,T2 ,T3 ,Gems ,stats ,runes ,runesSkill ,gameplayEn } = this.state;
    if (error.length < 0) {
      return <div className="alert alert-danger" role={"alert"} ><i className="bi bi-exclamation-triangle-fill"></i> Erreur : {error}</div>;
    } else if (!isLoaded) {
      return (
        <Container fluid>         
          <Row>
            <Col sm>
              <h2>Gameplay</h2>
              <ListGroup as={"ul"} variant="flush" className="w-100">
                {
                  Array.from({ length: 15 }, (index) =>
                    <Placeholder key={index} animation="glow" as={ListGroup.Item}><Placeholder sm={6} /></Placeholder>
                  )
                    
                }
              </ListGroup>
            </Col>
            <Col sm> 
              <h2>Skills</h2>
              <Table striped borderless hover variant="dark" responsive>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Name</th>
                    <th scope="col">Level</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <FontAwesomeIcon icon="fa-regular fa-circle-notch" />
                  </tr>
                </tbody>
              </Table>
            </Col>
          </Row>
          <hr/>
          <Row>
            <Col sm>
              <h2>Stats</h2>
              <p></p>
            </Col>
            <Col sm>
            <h2>Runes</h2>
              <Table striped borderless hover variant="dark" responsive>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Skill</th>
                    <th scope="col">Runes</th>
                  </tr>
                </thead>
                <tbody>
                </tbody>
              </Table>
            </Col>
          </Row>
          <hr />
          <Row>
            <Col sm>
              <h2>Gear Set</h2>
              <Row>
                <h3>Tier 1</h3>
                <Col sm><p></p></Col>
                <h3>Tier 2</h3>
                <Col sm><p></p></Col>
                <h3>Tier 3</h3>
                <Col sm><p></p></Col>
              </Row>
            </Col>
            <Col sm>
              <h2>Gems</h2>
              <Row>
                <Col >
                <h3>Attack</h3>

                </Col>
              </Row>
              <Row>
                <Col>
                  <h3>Cooldown</h3>

                </Col>
              </Row>
            </Col>
          </Row>
          <hr />
          <Row>
            
          </Row>
        </Container>
      );
    } else {
      return (
        <Container fluid>         
          <Row>
            <Col sm>
            <FontAwesomeIcon icon="fa-regular fa-circle-notch" />
              <h2>Gameplay</h2>
              <ListGroup as={"ul"} numbered variant="flush">
                {gameplayEn.map((element, index) => <ListGroup.Item action variant="dark" key={index} as={"li"}>{element}</ListGroup.Item>)}
              </ListGroup>
            </Col>
            <Col sm> 
              <h2>Skills</h2>
              <Table striped borderless hover variant="dark" responsive>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Name</th>
                    <th scope="col">Level</th>
                  </tr>
                </thead>
                <tbody>
                {
                    skillsName.map((element,index) =>
                      <tr key={index}>
                        <td className="ms-2 me-auto">
                          {index+1}
                        </td>
                        <td className="ms-2 me-auto">
                          {element}
                        </td>
                        <td className="ms-2 me-auto">
                          {skillsLevel[index]}
                        </td>
                      </tr>)
                  }
                </tbody>
              </Table>
            </Col>
          </Row>
          <hr/>
          <Row>
            <Col sm>
              <h2>Stats</h2>
              {stats.map(element =>  <p key={element}>{element}</p> )}
            </Col>
            <Col sm>
            <h2>Runes</h2>
              <Table striped borderless hover variant="dark" responsive>
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Skill</th>
                    <th scope="col">Runes</th>
                  </tr>
                </thead>
                <tbody>
                {
                    runesSkill.map((element,index) =>
                      <tr key={index}>
                        <td className="ms-2 me-auto">
                          {index+1}
                        </td>
                        <td className="ms-2 me-auto">
                          {element}
                        </td>
                        <td className="ms-2 me-auto">
                          {runes[index]}
                        </td>
                      </tr>)
                  }
                </tbody>
              </Table>
            </Col>
          </Row>
          <hr />
          <Row>
            <Col sm>
              <h2>Gear Set</h2>
              <Row>
                <h3>Tier 1</h3>
                {
                  T1.map((element, index) =>
                    <Col key={index+element} sm>
                      <p key={index}>{element}</p>
                    </Col>
                  )
                }
                <h3>Tier 2</h3>
                {
                  T2.map((element, index) =>
                    <Col key={index+element} sm>
                      <p key={index}>{element}</p>
                    </Col>
                  )
                }
                <h3>Tier 3</h3>
                {
                  T3.map((element, index) =>
                    <Col key={index+element} sm>
                      <p key={index}>{element}</p>
                    </Col>
                  )
                }
              </Row>
            </Col>
            <Col sm>
              <h2>Gems</h2>
              <Row>
                <Col >
                <h3>Attack</h3>
                {
                    Gems['Attacks'].map((element, index) => 
                          <p key={index}>{ element }</p>
                          )
                        }
                </Col>
              </Row>
              <Row>
                <Col>
                  <h3>Cooldown</h3>
                  {
                      Gems['Cooldown'].map((element, index) => 
                        <p key={index}>{ element }</p>
                        )
                      }
                </Col>
              </Row>
            </Col>
          </Row>
          <hr />
          <Row>
            
          </Row>
        </Container>
      );
    }
  }
};
export default LostArkClass;